import React, { Component } from 'react';
import Loader from '../components/Loader.js';
import './SingleSeries.css';
import { Link } from 'react-router-dom';
import Default from '../assets/default.jpg';

const SeasonItem=props=>(
  <li>
    <Link to={`/season/${props.season.id}`}>{props.season.image !== undefined && props.season.image !== null &&
       // console.log(props.season.image.medium)
       <img src={props.season.image.medium} alt=""/>
    }
    {props.season.image === undefined || props.season.image !== null ||
      <img style={{width:210, height:295}} src={Default} alt=""/>
    }</Link>
  </li>
)

class SingleSeries extends Component{
  state={
    show:null,
    detail:null
  }
  componentDidMount(){
    const id = this.props.match.params.id;
    fetch(`https://api.tvmaze.com/shows/${id}?embed=episodes`)
      .then(response=>response.json())
      .then(json=>this.setState({ show:json}));
    fetch(`https://api.tvmaze.com/shows/${id}/seasons`)
      .then(response=>response.json())
      .then(json=>this.setState({ detail:json}));
  }
  render(){
    const { show, detail } =this.state;
    //console.log(show);
    //console.log(detail);

    return (
      <div>
        {show ===null && <Loader />}
        {show !==null && detail !== null &&
          <div className="head">
            <div className="single">
              <img src={show.image.medium} alt="Show"/>
              <div>
                <h3>{show.name}</h3>
                <div className="detail">
                  <p>Premiered: {show.premiered}</p>
                  <p>Rating: {show.rating.average}</p>
                  <p>Seasons: {detail.length}</p>
                  <p>Episodes: {show._embedded.episodes.length}</p>
                </div>
                <div className="summary" dangerouslySetInnerHTML={{__html:show.summary}}></div>
              </div>
            </div>
            {detail !==null &&
              <ul className="series-item">
              {detail.map(season=>(
                  // console.log(show),
                    <SeasonItem key={season.id} season={season}/>
                 ))
              }
            </ul>}
          </div>

        }
      </div>
    )
  }
}

export default SingleSeries;



