import React, { Component } from 'react';
import Loader from '../components/Loader.js';
import { Link } from 'react-router-dom';
import Default from '../assets/default.jpg';
import './SingleSeason.css'


const SingleEpisode=props=>(
  <li className="singleepisode">
    <div className="image">
      {props.ep.image !== undefined && props.ep.image !== null &&
         // console.log(props.season.image.medium)
        <img src={props.ep.image.medium} alt=""/>
      }
      {props.ep.image === undefined || props.ep.image !== null ||
        <img src={Default} alt=""/>
      }
      <h3>{props.ep.name}</h3>
    </div>
    <div className="desc" dangerouslySetInnerHTML={{__html:props.ep.summary}}></div>
    {console.log(props.ep)}
  </li>
)


class SingleSeason extends Component{
  state={
    season:null
  }
  componentDidMount(){
    const id = this.props.match.params.id;
    fetch(`https://api.tvmaze.com/seasons/${id}/episodes`)
      .then(response=>response.json())
      .then(json=>this.setState({ season:json}));
  }
  render(){
    const { season } =this.state;

    return (
      <div>
      {season ===null && <Loader />}
      {season !==null && season[0] !==undefined &&
        <div className="head">
          <div>Season {season[0].season}</div>
          Loaded
          <ul>
            {season.map(ep=>(
              <SingleEpisode key={ep.id} ep={ep}/>
            ))}
          </ul>
        </div>

      }
      </div>
    )
  }
}

export default SingleSeason;



