import React from 'react';
import { Switch, Route } from 'react-router-dom';
import Series from '../containers/Series.js';
import SingleSeries from '../containers/SingleSeries.js';
import SingleSeason from '../containers/SingleSeason.js';

const Main = props =>(
  <Switch>
    <Route exact path={'/'} component={Series} />
    <Route exact path={'/series/:id'} component={SingleSeries} />
    <Route exaxt path={'/season/:id'} component={SingleSeason} />
  </Switch>
);

export default Main



